.hero-section {
  &__item {
    background-color: #eeeeee;
    width: 100%;
    position: relative;

    &::before {
      content: '';
      display: block;
      padding-top: 155%;
    }

    span {
      display: block;
    }
  }

  &__content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}