.hero-section__item {
  background-color: #eeeeee;
  width: 100%;
  position: relative;
}

.hero-section__item::before {
  content: '';
  display: block;
  padding-top: 155%;
}

.hero-section__item span {
  display: block;
}

.hero-section__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
